import React from "react";
import ProjectCard from "../ProjectCard/ProjectCard";
import "./Projects.css";
import Fade from "react-reveal/Fade";

const Projects = () => {
  return (
    <div className="project-card" id="projects">
      <Fade bottom>
        <h2>Projects</h2>
      </Fade>
      <div className="proj-card-wrapper">
        <div>
          <Fade left>
            <ProjectCard
              img="https://www.barnesandnoble.com/blog/wp-content/uploads/2022/10/PROD-24914_Blog_Social_BOTY_1488x852.jpg"
              des="Author & Attic is an e-commerce website that primarily sells and delivers books and accessories . This is collabrative project. We have created clone of Barnes and Noble website in 5 days. User able to signup and login successfully. Also user able to see, buy, add and make payment through website and admin side function is also added."
              head="Author & Attic (Barnes and Noble Clone)"
              deploy="https://gleeful-scone-fa6799.netlify.app/"
              git="https://github.com/vetrivelanarsu/plastic-bee-2809"
              stack={["HTML", "CSS", "JavaScript"]}
            />
          </Fade>
        </div>
        <div>
          <Fade right>
            <ProjectCard
              img="https://successpixel.com/wp-content/uploads/2018/02/Paytm-Top-Online-Shopping-Sites-in-India.jpg"
              des="Paytm Mall is India's premier shopping app for buying Gift Cards, brought to you by Paytm, India's most trusted brand. Shop with a smile for best deals. This is collabrative project. We have created clone of Paytm Mall website in 5 days. User able to signup and login successfully."
              head="Digital Dealz (Paytm Mall Clone)"
              deploy="https://dynamic-sunburst-a3c0da.netlify.app/"
              git="https://github.com/Himanshu04210/Paytm-mall-cloneapp"
              stack={["HTML", "CSS", "JavaScript"]}
            />
          </Fade>
        </div>
      </div>
    </div>
  );
};

export default Projects;
